import React, { useRef } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  full,
  fullAbsolute,
  imageSlide,
} from '../styles'

import { backgrounds } from '../assets'

import Builder from './Builder';

const { bg1/* , bg2, bg3  */ } = backgrounds

export default function BuildSite(props) {
  const ref = useRef()
  const { structure, refs } = props
  return (
    <div style={full}>
      {/* <Builder.Slides
        delay={10000}
        alt="site_background"
        slides={[bg1, bg2, bg3]}
      /> */}
      <img
        alt={`background`}
        style={{ ...imageSlide, ...fullAbsolute }}
        src={bg1}
      />
      <Swiper
        ref={ref}
        spaceBetween={0}
        slidesPerView={1}
        threshold={20}
        //nested
        /* hashNavigation={{
          watchState: true
        }} */
        direction="vertical"
        hashNavigation={{
          replaceState: true,
          watchState: true
        }}
        //loop
        style={full}
      //scrollbar={{ draggable: true, hide: true }}
      >{
          structure.map((section, index) => (
            <SwiperSlide
              {...({
                'data-hash': `section${index}`
              })} key={`section${index}`}
            >
              {
                (
                  typeof section === 'object'
                  && !Array.isArray(section)
                ) && (
                  <Builder.Section
                    index={index}
                    scenes={section.content}
                    refs={{ ...refs, site: ref }}
                    {...section.props}
                  />
                )
              }
              {
                (
                  typeof section === 'object'
                  && Array.isArray(section)
                ) && (
                  <Builder.Section
                    index={index}
                    scenes={section}
                    refs={{ ...refs, site: ref }}
                  />
                )
              }
            </SwiperSlide>
          ))
        }</Swiper>
    </div>
  )
}