import React, { useRef, useEffect } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  overlay,
  full,
  center,
  title,
  heading,
  //palette,
  foreground,
  background,
  fullAbsolute,
  text,
  box,
  border
} from '../styles'

import { Builder } from '../components'
