const configs = {
  landscape: {
    slidesPerView: 1,
    slidesPerColumn: 2,
    /* slidesPerView: 1,
    //slidesPerGroup: 2,
    slidesPerColumn: 2, */
    //slidesPerColumnFill: 'column'
  },
  portrait: {
    slidesPerView: 2,
    //slidesPerGroup: 2,
    //slidesPerColumn: 2,
  },
  square: {
    slidesPerView: 1,
    //slidesPerGroup: 2,
    slidesPerColumn: 1,
  }
}

export default function Encartes({ dimensions, item, refs, sectionIndex }) {
  const ref = useRef()
  useEffect(() => {
    ref.current.swiper.update()
    ref.current.swiper.changeDirection(
      dimensions.orientation === 'landscape'
        ? "vertical"
        : "horizontal")
  }, [dimensions])

  function addToCart() {
    (refs?.site?.current?.swiper).slideNext();
    //console.log(refs?.[`section${sectionIndex}`]?.current?.swiper)
    //(refs?.[`section${sectionIndex + 1}`]?.current?.swiper).slideTo(0);
  }

  return (
    <div style={{ ...center, ...full }}>
      <div style={{ ...full, ...overlay, display: 'flex'/* , flexDirection: 'column'  */ }}>
        {/* <div style={{ position: 'absolute', top: 0, left: 0, zIndex: 2 }}>
          <div style={heading}>
            {item.details[0]}
          </div>
        </div> */}
        <div style={full}>
          <Swiper
            spaceBetween={0}
            freeMode
            //nested
            //zoom
            direction="vertical"
            style={full}
            breakpoints={{
              '@0': configs.portrait,
              '@0.9': configs.square,
              '@1.1': configs.landscape
            }}
          //scrollbar={{ draggable: true }}
          >
            <SwiperSlide
              style={{
                ...full, ...center,
                zIndex: 2,
                ...(
                  dimensions.orientation === 'landscape'
                    ? { width: '50%' }
                    : {})
              }}
            >
              <div
                style={{
                  transform: 'skewY(2deg)',
                  width: '85%'
                }}
              >
                <div style={{
                  ...foreground.primary,
                  ...heading,
                  padding: 0
                }}>{item.name}</div>
                <div style={{
                  padding: '5%',
                  position: 'relative'
                }}>
                  <div style={{

                    ...background.primary,
                    ...border.radius,
                    ...fullAbsolute,
                    ...box.shadow,
                    opacity: 0.85
                  }} />
                  {
                    item.details.map((detail, index) => {
                      const { name, value, highlight } = detail
                      return (
                        <div
                          key={`${name}_${index}`}
                          style={{
                            ...title,
                            ...foreground.secondary,
                            display: 'flex',
                            flexDirection: 'row',
                            justifyContent: 'space-between'
                          }}>
                          <div style={foreground.secondary}>
                            {name}
                          </div>
                          <div
                            style={{
                              ...foreground.light,
                              ...text.shadow,
                              ...(!highlight ? {} : { fontSize: '10vmin' })
                            }}
                          >
                            {value}
                          </div>
                        </div>
                      )
                    })
                  }
                  <div
                    style={{
                      cursor: 'pointer',
                      ...title,
                      ...background.secondary,
                      ...foreground.light,
                      ...text.shadow,
                      ...box.shadow,
                      ...border.radius,
                      textAlign: 'center'
                    }}
                    onClick={addToCart}
                  >
                    EU QUERO
                </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide
              style={
                dimensions.orientation === 'landscape'
                  ? { /* pointerEvents: 'none', */ width: '50%' }
                  : { /* pointerEvents: 'none' */ }
              }
            >
              <Builder.Slides
                useRef={ref}
                slides={item.images}
                style={{
                  zIndex: 1,
                  //...box.shadowFilter
                }}
                speed={500}
                imageStyle={{
                  objectFit: 'contain',
                  width: '90%',
                  height: '90%',
                  margin: '5%',
                  //pointerEvents: 'all'
                }}
                //nested
                zoom={{
                  toggle: false,
                  maxRatio: 2
                }}
                effect="slide"
                scrollbar={{ draggable: true/* , hide: true */ }}
                direction={
                  dimensions.orientation === 'landscape'
                    ? "vertical"
                    : "horizontal"
                }
              /* fadeEffect={{
                crossFade: true
              }} */
              />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div >
  )
}