import React, { useRef, useEffect } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  overlay,
  full,
  center,
  heading,
  title,
  //palette,
  foreground,
  background,
  fullAbsolute,
  text,
  box,
  border,
  palette
} from '../styles'

import { Builder } from '../components'
/* 
const square = {
  backgroundColor: palette.secondary,
  //width: '80vmin',
  //height: '80vmin',
  width: '90%',
  height: '90%',
} 
const container = {
  display: 'flex',
  flexDirection: 'row',
  flexWrap: 'wrap',
  justifyContent: 'center',
  alignItems: 'center'
}*/

const configs = {
  landscape: {
    slidesPerView: 1,
    slidesPerColumn: 2,
    /* slidesPerView: 1,
    //slidesPerGroup: 2,
    slidesPerColumn: 2, */
    //slidesPerColumnFill: 'column'
  },
  portrait: {
    slidesPerView: 2,
    //slidesPerGroup: 2,
    //slidesPerColumn: 2,
  },
  square: {
    slidesPerView: 1,
    //slidesPerGroup: 2,
    slidesPerColumn: 1,
  }
}

export default function Encartes({ dimensions, item, refs }) {
  const ref = useRef()
  useEffect(() => {
    ref.current.swiper.update()
    ref.current.swiper.changeDirection(
      dimensions.orientation === 'landscape'
        ? "vertical"
        : "horizontal")
  }, [dimensions])

  function paymentMethods() {
    (refs?.section?.current?.swiper).slideNext()
  }

  return (
    <div style={{ ...center, ...full }}>
      <div style={{ ...full, ...overlay, display: 'flex'/* , flexDirection: 'column'  */ }}>
        {/* <div style={{ position: 'absolute', top: 0, left: 0, zIndex: 2 }}>
          <div style={heading}>
            {item.details[0]}
          </div>
        </div> */}
        <div style={full}>
          <Swiper
            spaceBetween={0}
            freeMode
            //nested
            //zoom
            direction="vertical"
            style={full}
            breakpoints={{
              '@0': configs.portrait,
              '@0.9': configs.square,
              '@1.1': configs.landscape
            }}
          //scrollbar={{ draggable: true }}
          >
            <SwiperSlide
              style={{
                ...full, ...center,
                zIndex: 2,
                ...(
                  dimensions.orientation === 'landscape'
                    ? { width: '50%' }
                    : {})
              }}
            >
              <div
                style={{
                  //transform: 'skewY(2deg)',
                  width: '85%'
                }}
              >
                <div style={{
                  fontSize: '8vmin',
                  ...foreground.primary,
                  ...heading,
                  padding: 0
                }}>{item.name}</div>
                <div style={{
                  padding: '5%',
                  position: 'relative'
                }}>
                  <div style={{

                    ...background.primary,
                    ...border.radius,
                    ...fullAbsolute,
                    ...box.shadow,
                    opacity: 0.85
                  }} />
                  {
                    item.details.map((detail, index) => {
                      const { name, value, highlight } = detail
                      return (
                        <div
                          key={`${name}_${index}`}
                          style={{
                            ...title,
                            ...foreground.secondary,
                            display: 'flex',
                            flexDirection: 'row',
                            justifyContent: 'space-between'
                          }}>
                          <div style={foreground.secondary}>
                            {name}
                          </div>
                          <div
                            style={{
                              ...foreground.light,
                              ...text.shadow,
                              ...(!highlight ? {} : { fontSize: '10vmin' })
                            }}
                          >
                            {value}
                          </div>
                        </div>
                      )
                    })
                  }
                  <div
                    style={{
                      cursor: 'pointer',
                      ...title,
                      ...background.secondary,
                      ...foreground.light,
                      ...text.shadow,
                      ...box.shadow,
                      ...border.radius,
                      textAlign: 'center'
                    }}
                    onClick={paymentMethods}
                  >
                    COMPRAR AGORA
                </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide
              style={{
                ...(
                  dimensions.orientation === 'landscape'
                    ? { /* pointerEvents: 'none', */ width: '50%' }
                    : { /* pointerEvents: 'none' */ /* height: '50%' */ }
                )
              }}
            >
              <Builder.Slides
                useRef={ref}
                slides={item.images}
                style={{
                  zIndex: 1,
                  //...box.shadowFilter
                }}
                speed={500}
                autoplay={false}
                scrollbar={{ draggable: true/* , hide: true */ }}
                imageStyle={{
                  objectFit: 'contain',
                  width: '80%',
                  height: '80%',
                  margin: '10%',
                  //pointerEvents: 'all'
                }}
                slideProps={{
                  style: {
                    width: '50%',
                    height: '50%'
                  }
                }}
                slidesPerView={2.5}
                slidesPerColumn={2}
                //nested
                /* zoom={{
                  toggle: false,
                  maxRatio: 2
                }} */
                freeMode
                effect="slide"
                direction={
                  dimensions.orientation === 'landscape'
                    ? "vertical"
                    : "horizontal"
                }
                renderItem={({ content }, index) => {
                  return (
                    <div style={{
                      ...full,
                    }}>
                      <div
                        style={{
                          ...fullAbsolute,
                          zIndex: -1,
                          borderRadius: '1vmin',
                          width: '90%',
                          height: '90%',
                          margin: '5%',
                          backgroundColor: palette.light,
                          ...box.shadow
                          //opacity: '0.8'
                        }}
                      />
                      <div
                        style={{
                          position: 'absolute',
                          //bottom: 0,
                          //right: 0,
                          padding: '6%',
                          color: palette.alert
                        }}
                      >
                        <div
                          onClick={() => item.removeItem(index)}
                          style={{
                            padding: '1vmin',
                            color: palette.light,
                            //border: 'solid 1px red',
                            borderRadius: '1vmin',
                            backgroundColor: 'rgba(255,0,0,0.5)',
                            cursor: 'pointer'
                          }}
                        >Remover item</div>
                      </div>
                      {content}
                    </div>
                  )
                }}
              /* fadeEffect={{
                crossFade: true
              }} */
              />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div >
  )
}