import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  full,
  fullAbsolute,
  imageSlide,
} from '../styles'

export default function Slides(props) {
  const {
    alt = 'background',
    zoom,
    imageStyle,
    useRef,
    slides,
    style,
    delay = 5000,
    slideProps,
    renderItem,
    ...rest
  } = props

  //const [zoomed, setZoomed] = useState(false)

  function toggle() {
    const { current = { swiper: { zoom: { out: console.error } } } } = useRef
    if (current.swiper.zoom.scale === 1/*  && !zoomed */) {
      //setZoomed(true)
      current.swiper.zoom.in()
      current.swiper.autoplay.stop()
      console.log(current.swiper.autoplay)
    } else if (current.swiper.zoom.scale > 1 /* && zoomed */) {
      //setZoomed(false)
      current.swiper.zoom.out()
      current.swiper.autoplay.start()
    }

  }

  const zoomProps = {
    className: "swiper-zoom-container",
    onDoubleClick: toggle
  }

  return (
    <Swiper
      ref={useRef}
      threshold={10}
      spaceBetween={0}
      slidesPerView={1}
      direction="horizontal"
      effect="fade"
      //loop
      speed={2500}
      autoplay={{ delay }}
      zoom={zoom}
      style={{
        ...full,
        ...fullAbsolute,
        ...(
          zoom ? { overflow: 'visible' } : {}
        ),
        ...style
      }}
      {...rest}
    >
      {
        slides.map((slide, index) => {
          const content = (
            <img
              src={slide}
              alt={`${alt}_slide_${index}`}
              style={{ ...imageSlide, ...imageStyle }}
            />
          )
          return (
            <SwiperSlide key={`${alt}_slide_${index}`} {...slideProps}>
              <div {...(!zoom ? { style: full } : zoomProps)}>
                {!renderItem ? content : renderItem({ content }, index)}
              </div>
            </SwiperSlide>
          )
        })
      }
    </Swiper>
  )
}
