function RenderField(field) {
  const [open, setOpen] = useState(false)
  const [value, setValue] = useState('')
  const toggleSelect = useCallback(() => {
    setOpen(!open)
  }, [open, setOpen])
  function changeSelect(event) {
    setValue(event.target.value)
  }
  return (
    <div>
      <FormControl variant="filled" color="primary" style={{ width: '100%' }}>
        <InputLabel id={`label${field.name}`}>
          {field.label}
        </InputLabel>
        {
          !field.select && (
            <FilledInput
              labelId={`label${field.name}`}
              id={`label${field.name}`}
            />
          )
        }
        {
          field.select && (
            <Select
              labelId={`label${field.name}`}
              id={`select${field.name}`}
              value={value}
              open={open}
              onClick={toggleSelect}
              onChange={changeSelect}
            >
              <MenuItem value="">Escolha uma opção</MenuItem>
              {
                field.select.map(option => (
                  <MenuItem
                    key={`option${field.name}${option.value}`}
                    value={option.value}
                  >
                    {option.label}
                  </MenuItem>
                ))
              }
            </Select>
          )
        }
      </FormControl>
      <div style={{ paddingBottom: '2%' }} />
    </div>
  )
}

